






























































import Vue from "vue";
import Component from "vue-class-component";
import { AppService } from "@/service/app.service";
import { FileModel, PostEditModel } from "@/x-vue/services/interfaces";
import LoginFirst from "@/x-vue/components/user/LoginFirst.vue";
import UploadButton from "@/components/UploadButton.vue";
import FileDisplay from "@/components/forum/FileDisplay.vue";

@Component({
  components: {
    LoginFirst,
    UploadButton,
    FileDisplay,
  },
})
export default class PostEdit extends Vue {
  form: PostEditModel = new PostEditModel();
  app = AppService.instance;

  submitted = false;
  uploadProgress = 0;

  uploadedFiles: Array<FileModel> = [];

  async loadPost(): Promise<void> {
    const post = await this.app.api.postGet({ idx: this.form.idx });
    this.form.idx = post.idx;
    this.form.title = post.title;
    this.form.content = post.content;
    this.form.files = post.files.map((file) => `${file.idx}`).join(",");

    this.uploadedFiles = post.files;
    // console.log("Update form: ", this.form);
  }

  mounted(): void {
    const idOrCategory = this.$route.params.idOrCategory;
    if (isNaN(parseInt(idOrCategory))) {
      this.form.categoryId = idOrCategory;
    } else {
      this.form.idx = idOrCategory;
      this.loadPost();
    }
  }

  async onSubmit(): Promise<void> {
    this.submitted = true;
    try {
      const res = await this.app.api.postEdit(this.form);
      // console.log("Post Edit: ", res);
      this.$router.push(`/${res.path}`).catch(() => null);
      this.submitted = false;
    } catch (e) {
      this.app.error(e);
      this.submitted = false;
    }
  }

  onFileUploaded(file: FileModel): void {
    this.form.files = this.app.addByComma(this.form.files, file.idx);
    this.uploadedFiles.push(file);
    this.uploadProgress = 0;
  }

  onFileDeleted(idx: string): void {
    this.form.files = this.app.deleteByComma(this.form.files, idx);
    const index = this.uploadedFiles.findIndex((file) => file.idx == idx);
    this.uploadedFiles.splice(index, 1);
  }
}
